              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="timeValue" :rules="rules.timeValue">
          <el-date-picker v-model="params.timeValue" @change="changetime" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <transition name="fade">
          <div class="me_hidden" v-show="isShow">
            <el-form-item label="仓库名称" size="mini">
              <el-select v-model="params.warehouse_id" filterable clearable placeholder="请选择" @focus="warehouseInvok" @change="warehouseChangeId">
