              <el-select v-model="params.allocation_id" @focus="InventoryInvok({company_id: params.company_id})" @change="allocationChangeId" filterable remote clearable placeholder="请输入">
                <el-option v-for="item in allocationInventoryList" :key="item.allocation_id" :label="item.allocation_name" :value="item.allocation_id">
                </el-option>
