              <getBrand v-model='params.brand_id' @change="brand_idChangeId"  />
            </el-form-item>
            <el-form-item label="商品名称" size="mini">
              <v-GoodsList :scopes='{row: params }' :isFuzzy='true' :vxeParams='{company_id: params.company_id}' :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
            </el-form-item>
            <el-form-item label="年周/批次" size="mini">
              <el-input v-model.trim="params.batch" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="单据号" size="mini">
              <el-input v-model="params.receipt_sn" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="单据状态" size="mini">
              <el-select v-model="params.auditor_status" clearable>
                <el-option label="全部" :value="0">全部</el-option>
