        {{ isShow? "收起":"显示全部"}}
        <b class="me_showIcon" :class="isShow?'el-icon-caret-top':'el-icon-caret-bottom'"></b>
      </div>
    </div>
    <!-- 操作按钮 -->
    <div class="add_delet" v-if="authority.b_export==1 && listShow">
      <el-dropdown placement="bottom-start">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="table_container" v-show="listShow">
      <xTable v-if='tableColumn.length > 0' @getData='getTableData' :ajax='{ url: "getSalesProfitListV2", params: params }' :footerMethod='footerMethod' :isShowTool='true' ref='xTable' :tableColumn='tableColumn' />
      <div class="table_info">
        <el-row type="flex" class="row-bg">
          <el-col :span="12" v-if="timeValue">
            <div>业务起始时间:{{params.start_time}}</div>
          </el-col>
          <el-col :span="12" v-if="timeValue">
            <div class="textAr">业务截止时间:{{params.end_time}}</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg" v-if="!!footUser">
          <el-col :span="12">
            <div>生成时间：{{footUser.search_time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">制表人：{{footUser.make_user}}</div>
          </el-col>
        </el-row>
      </div>
