</template>
<script>
import { opthionList } from "common/utils/public_function/commonUtils";
import getAreaList from "common/components/public/getAreaList";
import { TableHead } from "./config/salesdetrpt";
export default {
  name: "reportSalesdetrpt",
  mixins: [TableHead],
  data() {
    return {
      isShow: false, // 控制要隐藏的查询
      authority: {},
      defaultArea: [0], //默认的区域id
      auditValue: null,
      searchParams: {},
      TableDataSum: null,

      footUser: {},
      listShow: false,
      business_type: null,
      timeValue: [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 30),
        new Date()
      ],
      propactive: { value: "value", children: "cities", checkStrictly: true },
      exportType: null,
      goPage: 1, //默认搜索页面
      loading: false
    };
  },
  components: {
    getAreaList //区域列表
  },
  methods: {
    // 搜索框收起展开
    showOrHidden() {
      this.isShow = !this.isShow;
    },
    changeDefaultArea(val) {
      this.params.province_id = val[0];
      this.params.city_id = val[1];
      this.params.area_id = val[2];
    },
    changebusiness(val) {
      this.params.business_type_ids = val.join(",");
    },
    getTableData({ data }) {
      this.footUser = data.data ? data.data.desc : {};
      // this.$refs.xTable._proxy("updateFooter");
    },
    querySearch(queryString, cb) {
      this.$set(this.params, "goods_id", "");
      this.$axMethod
        .getAllGoodsList({
          company_id: this.params.company_id,
          goods_name: queryString
        })
        .then(res => {
          let item = {
            goods_id: "编号",
            goods_name: "商品名称",
            disabled: true
          };
          res.data.data.unshift(item);
          cb(res.data.data);
        });
    },
    handleSelect(value) {
      this.$set(this.params, "goods_id", value.goods_id);
    },
    changetime(val) {
      //选择的日期
      if (val != null) {
        this.params.start_time = val[0];
        this.params.end_time = val[1];
      }
    },
    companyChange(val) {
      this.$set(this.params, "warehouse_id", ""); // 初始化仓库名称
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选择项
      this.$set(this.params, "employee_id", ""); // 初始化业务员下拉选中项
      this.$set(this.params, "allocation_id", ""); // 初始化货位
      this.$set(this.params, "brand_id", ""); // 初始化品牌
      this.$set(this.params, "goods_name", ""); // 初始化商品名称
      this.$set(this.params, "batch", ""); // 初始化批次
    },
    warehouseChangeId(val) {
      // 仓库 change
      this.$set(this.params, "allocation_id", ""); // 初始化货位
      this.$set(this.params, "brand_id", ""); // 初始化品牌
      this.$set(this.params, "goods_name", ""); // 初始化商品名称
      this.$set(this.params, "batch", ""); // 初始化批次
    },
    allocationChangeId(val) {
      // 货位 change
      this.$set(this.params, "brand_id", ""); // 初始化品牌
      this.$set(this.params, "goods_name", ""); // 初始化商品名称
      this.$set(this.params, "batch", ""); // 初始化批次
    },
    brand_idChangeId(val) {
      // 品牌名称 change
      this.$set(this.params, "goods_name", ""); // 初始化商品名称
      this.$set(this.params, "batch", ""); // 初始化批次
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error"
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      s = Object.assign({}, s); // 克隆数据避免数据引用
      this.$axMethod.exportSalesProfitUrl(s).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    departmentChange() {
      this.$set(this.params, "employee_id", "");
    },
    timeF() {
      if (this.auditValue) {
        this.params.auditor_start_time = this.auditValue[0];
        this.params.auditor_end_time = this.auditValue[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        auditor_status: 2,
        company_id: [Number(localStorage.company_id)],
        start_time: this.formData(this.timeValue[0]),
        end_time: this.formData(this.timeValue[1]),
        timeValue: [
          this.formData(this.timeValue[0]),
          this.formData(this.timeValue[1])
        ]
      });
      this.business_type = [];
      this.defaultArea = [0];
      this.auditValue = [];
      this.onSearch("TableDataRef");
    },
    totleSum() {
      let obj = Object.assign({}, this.params);
      this.$axMethod.getAllSalesProfitList(obj).then(({ data }) => {
        if (data.code == 200) {
          this.TableDataSum = data.data;
          setTimeout(() => {
            this.$refs.xTable._proxy("updateFooter");
          }, 50);
        }
      });
    },
    onSearch() {
      // this.footUser = {};
      this.timeF();
      this.$refs["TableDataRef"].validate(valid => {
        if (valid) {
          this.listShow = true;
          this.$refs.xTable._getData();
          this.totleSum();
        } else {
          return false;
        }
      });
    },
    formData(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;
      return [year, month, day].join("-");
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      auditor_status: 2,
      company_id: [Number(localStorage.company_id)],
      start_time: this.formData(this.timeValue[0]),
      end_time: this.formData(this.timeValue[1]),
      timeValue: [
        this.formData(this.timeValue[0]),
        this.formData(this.timeValue[1])
      ]
    });
    this.totleSum();
    this.companyListInvok();
    opthionList(this, localStorage);
  }
};
</script>

